<template><div><h2>debounce</h2>
<div class="warning">
<p>请注意了解 <code>debounce</code> 和 <code>throttle</code> 的区别。如果尚不清楚，请<code>google</code>之。</p>
</div>
<pre class="hljs"><code><span class="hljs-keyword">import</span> { debounce } <span class="hljs-keyword">from</span> <span class="hljs-string">'vux'</span>

